<template>
  <div id="canada_ship">
    <div class="topBtn">
      <el-button type="primary" @click="search_ship">所有</el-button>
      <el-button type="primary" @click="search_noship">未发货</el-button>
      <el-button type="primary" @click="search_nevership">不发</el-button>
      <el-button type="primary" @click="search_errorOrder">错误订单</el-button>
      <el-button type="primary" @click="go_roure('/Warehouse_data')"
        >仓库数据</el-button
      >
      <el-button type="primary" @click="exportData()">点击导出</el-button>
      <el-button type="primary" @click="exportData('1')">点击导出1</el-button>
    </div>
    <div class="table_data">
      <el-table
        ref="notErrorTable"
        v-show="!errorTable"
        max-height="720"
        border
        :data="tableData"
        style="width: 100%"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <!-- <el-table-column type="index" label="序号" align="center" width="60"> -->
        <!-- </el-table-column> -->
        <el-table-column prop="id" align="center" label="id" width="60">
        </el-table-column>
        <el-table-column
          prop="customer_order_number"
          align="center"
          label="订单号"
        >
        </el-table-column>
        <el-table-column prop="contact_name" align="center" label="姓名">
        </el-table-column>

        <el-table-column prop="phone_number" align="center" label="电话号码">
        </el-table-column>

        <el-table-column
          prop="country_code"
          align="center"
          label="国家"
          width="60"
        >
        </el-table-column>
        <el-table-column prop="city" align="center" label="城市">
        </el-table-column>
        <!-- <el-table-column
          prop="customer_order_number"
          align="center"
          label="订单号"
        >
        </el-table-column> -->

        <el-table-column prop="state" align="center" label="州/省">
        </el-table-column>

        <el-table-column prop="street" align="center" label="详细地址">
        </el-table-column>

        <el-table-column
          prop="zip_code"
          align="center"
          label="邮编"
          width="100"
        >
        </el-table-column>
        <el-table-column prop="sku" align="center" label="sku">
        </el-table-column>
        <el-table-column prop="tracking_no" align="center" label="追踪编码">
        </el-table-column>

        <el-table-column width="220" label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editData(scope.row)"
              >编辑</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="add_tracing_no(scope.row)"
              >增加追踪编号</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="updateStatus(scope.row.id)"
              >更新发货状态</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-table
        ref="ErrorTable"
        v-show="errorTable"
        max-height="720"
        border
        :data="tableData"
        style="width: 100%"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <!-- <el-table-column type="index" label="序号" align="center" width="60"> -->
        <!-- </el-table-column> -->
        <el-table-column prop="id" align="center" label="id" width="100">
        </el-table-column>
        <el-table-column
          prop="customer_order_number"
          align="center"
          label="订单号"
        >
        </el-table-column>
        <el-table-column prop="failure_reason" align="center" label="错误原因">
        </el-table-column>
        <el-table-column width="220" label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editData(scope.row)"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @current-change="changePage"
        :current-page="currentPage"
        :page-size="50"
        layout="total, prev, pager, next, jumper"
        :total="total_num"
      >
      </el-pagination>
    </div>
    <el-dialog title="数据编辑" :visible.sync="dialogVisible" width="60%">
      <el-form
        :inline="true"
        :model="editForm"
        :rules="rules"
        label-width="100px"
        ref="CanadaShipDialog"
      >
        <el-form-item label="姓名" prop="contact_name">
          <el-input v-model="editForm.contact_name"></el-input>
        </el-form-item>
        <el-form-item label="电话号码" prop="phone_number">
          <el-input v-model="editForm.phone_number"></el-input>
        </el-form-item>
        <el-form-item label="城市" prop="city">
          <el-input v-model="editForm.city"></el-input>
        </el-form-item>
        <el-form-item label="州/省" prop="state">
          <el-input v-model="editForm.state"></el-input>
        </el-form-item>
        <el-form-item label="街道" prop="street">
          <el-input v-model="editForm.street"></el-input>
        </el-form-item>
        <el-form-item label="邮编" prop="zip_code">
          <el-input v-model="editForm.zip_code"></el-input>
        </el-form-item>
        <el-form-item label="sku" prop="sku">
          <el-input v-model="editForm.sku"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitData('CanadaShipDialog')"
            >提交</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="数据编辑" :visible.sync="dialogVisible1" width="40%">
      <el-form :inline="true" :model="tracing_no_form">
        <el-form-item label="追踪编码">
          <el-input v-model="tracing_no_form.tracking_no"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addTracingNo">提交</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="导出数据" :visible.sync="exportIdVisible" width="40%">
      <el-form :inline="true">
        <el-form-item label="id">
          <el-input v-model="orderId"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitId">下载</el-button>
        </el-form-item>
      </el-form>
      <a
        ref="downloadA"
        style="    z-index: -1;
    position: absolute;
    top: 0;"
        :href="downloadLink"
        download=""
        >导出</a
      >
    </el-dialog>
    <el-dialog
      title="确认更新？"
      :visible.sync="updateStatusDialog"
      width="400px"
    >
      <div
        style="display: flex;
    justify-content: space-evenly;width: 280px;
    margin: 0 auto;"
      >
        <el-button style="margin-left:0" @click="updateStatusDialog = false"
          >取 消</el-button
        >
        <el-button style="margin-left:50px" type="primary" @click="updateSubmit"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      errorTable: false,
      updateStatusDialog: false,
      id: "",
      loading: false,
      ship_status: 2, // 已发货 2 未发货 1 不发货0  错误订单 3
      currentPage: 1,
      total_num: 0,
      row: {},
      dialogVisible: false,
      tableData: [],
      // 编辑弹框
      editForm: {},
      // 弹框验证

      rules: {
        contact_name: [
          { required: true, message: "请输入姓名", trigger: "blur" }
        ],
        phone_number: [
          { required: true, message: "请输入手机号码", trigger: "blur" }
        ],
        city: [{ required: true, message: "请输入城市", trigger: "blur" }],
        state: [{ required: true, message: "请输入州/省", trigger: "blur" }],
        street: [{ required: true, message: "请输入街道", trigger: "blur" }],
        zip_code: [{ required: true, message: "请输入邮编", trigger: "blur" }],
        sku: [{ required: true, message: "请输入sku", trigger: "blur" }]
      },
      // 追踪编码表单
      dialogVisible1: false,
      tracing_no_form: {},
      // 下载订单：
      orderId: "",
      exportIdVisible: false,
      downloadLink: "", // 下载链接,
      requestUrl: ""
    };
  },
  methods: {
    updateSubmit() {
      let url = this._url2 + "server/order_management/ca_update_state/";
      this.$axios
        .get(url, {
          params: {
            id: this.id
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message.success(res.data.msg);
            this.updateStatusDialog = true;
          } else {
            this.$message.warning("请求未成功");
          }
        });
    },
    updateStatus(id) {
      this.id = id;
      this.updateStatusDialog = true;
    },
    exportData(num) {
      this.exportIdVisible = true;
      if (num && num === "1") {
        this.requestUrl = "server/order_management/ca_export_data1/";
      } else {
        this.requestUrl = "server/order_management/ca_export_data/";
      }
    },
    submitId() {
      this.exportIdVisible = false;
      // 下载请求。
      if (this.orderId === "") {
        alert("请输入ID号");
        return;
      }

      this.$axios
        .get(this._url2 + this.requestUrl, {
          params: {
            id: this.orderId
          }
        })
        .then(res => {
          this.orderId = "";
          if (res.data.code === 200) {
            this.downloadLink = this._url2 + res.data.data;
            console.log(this.downloadLink);
            if (this.downloadLink) {
              this.$nextTick(() => {
                this.$refs.downloadA.click();
              });
            }
          } else {
            alert("请求错误");
          }
        });
    },

    changePage(val) {
      let status = "";
      this.currentPage = val;
      // 发送请求
      if (this.ship_status === 2) {
        // 已发货
        status = "已发货";
      } else if (this.ship_status === 1) {
        // 未发货
        status = "未发货";
      } else if (this.ship_status === 0) {
        status = "不发";
      } else {
        status = "错误订单";
      }
      this.getTableData(status);
    },

    go_roure(val) {
      this.$router.push(val);
    },

    shipFun(statusNum, statusWord) {
      if (!this.ship_status !== statusNum) {
        this.currentPage = 1;
      }
      this.ship_status = statusNum;
      this.getTableData(statusWord);
    },
    // 发货。

    search_ship() {
      this.shipFun(2, "已发货");
    },
    search_nevership() {
      this.shipFun(0, "不发");
    },
    search_errorOrder() {
      this.shipFun(3, "错误订单");
    },
    search_noship() {
      this.shipFun(1, "未发货");
    },
    // 编辑弹框
    editData(row) {
      this.row = row;
      // 弹出弹框
      this.dialogVisible = true;
      for (let key in row) {
        this.$set(this.editForm, key, row[key]);
      }
      //   this.editForm = row;
    },
    submitData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.dialogVisible = false;
          // 发送请求
          let formData = new FormData();
          //   需要在重新赋值给绑定的row
          //   for (let key in this.rowData) {
          //     // this.row[key] = this.warehouse_form[key];
          //     this.$set(this.rowData, key, this.warehouse_form[key]);
          //     fd.append(key, this.warehouse_form[key]);
          //   }
          for (let key in this.editForm) {
            // 将弹框中的内容在赋值给绑定的row
            this.row[key] = this.editForm[key];

            console.log(key);
            if (key === "phone_number") {
              formData.append("phone", this.editForm[key]);
            }
            if (
              key === "id" ||
              key === "contact_name" ||
              key === "city" ||
              key === "state" ||
              key === "street" ||
              key === "zip_code" ||
              key === "sku"
            ) {
              formData.append(key, this.editForm[key]);
            }
          }

          if (this.ship_status === 3) {
            // 错误订单请求
            formData.append("status", "error");
          }
          this.$axios
            .post(
              this._url2 + "server/order_management/ca_shipment_update_data/",
              formData
            )
            .then(res => {
              if (res.data.code === 200) {
                alert("提交成功！");
              } else {
                alert("提交失败");
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 追踪编码弹框
    add_tracing_no(row) {
      // 弹出弹框
      // if (row.tracking_no) {
      //   return;
      // }
      this.dialogVisible1 = true;
      this.tracing_no_form = row;
    },

    addTracingNo() {
      this.dialogVisible1 = false;
      let url = this._url2 + "server/order_management/ca_update_tracking_no/";
      if (
        !this.tracing_no_form.tracking_no ||
        this.tracing_no_form.tracking_no.length === 0
      ) {
        alert("请输入追踪编号");
        return;
      }
      let fd = new FormData();
      fd.append("id", this.tracing_no_form.id);
      fd.append("tracking_no", this.tracing_no_form.tracking_no);
      this.$axios.post(url, fd).then(res => {
        if (res.data.code === 200) {
          alert("增加追踪编号成功");
        } else {
          alert("增加失败");
        }
      });
    },

    getTableData(status) {
      if (this.ship_status === 3) {
        this.errorTable = true;
      } else {
        this.errorTable = false;
      }
      this.loading = true;
      let url = "server/order_management/ca_shipment_data/";
      this.$axios
        .get(this._url2 + url, {
          params: {
            status,
            page: this.currentPage
          }
        })
        .then(res => {
          console.log(res);
          this.loading = false;
          this.tableData = res.data.data || [];
          // notErrorTable
          if (this.ship_status === 3) {
            this.$nextTick(() => {
              this.$refs["ErrorTable"].bodyWrapper.scrollTop = 0;
            });
          } else {
            this.$nextTick(() => {
              this.$refs["notErrorTable"].bodyWrapper.scrollTop = 0;
            });
          }
          if (res.data.nums.length > 0) {
            this.total_num = res.data.nums[0].all_num;
          }
        });
    }
  },
  created() {
    this.getTableData("已发货");
  }
};
</script>

<style scope>
#canada_ship {
  margin: 22px 0;
}
#canada_ship .topBtn {
  margin-bottom: 20px;
}
</style>
<style>
#canada_ship .el-pagination {
  background-color: rgba(255, 255, 255, 0.9);
}
#canada_ship .el-dialog__header {
  padding: 20px 20px 10px;
}
#canada_ship .el-input.is-disabled .el-input__inner {
  background-color: transparent;
  border-color: transparent;
  color: #606266;
  cursor: default;
}
#canada_ship .el-dialog .el-form {
  margin-left: 65px;
}
#canada_ship .el-dialog .el-button {
  width: 202px;
  margin-left: 100px;
}
</style>



